*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Placeholder", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.drink2go-button {
  margin: 0;
  text-align: center;
  text-decoration: none;
  color: #ffffff;
  background: #7859cf;
  border: 0;
  cursor: pointer;
  font-family: inherit;
}

.wrapper {
  position: relative;
  background-color: rgba(169, 169, 169, 0.508);
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

/* Стиль для кнопки возврата */
.back-button {
  position: fixed;
  top: 50px;
  right: 50px;
  background-color: #41b3a3; /* Цвет фона кнопки */
  color: #fff; /* Цвет текста */
  padding: 10px 20px; /* Отступы вокруг текста */
  text-decoration: none; /* Убирает подчеркивание */
  border-radius: 15px 15px 15px 15px;
  border-bottom: 5px solid #1beed0;
  border-right: 5px solid #1beed0;
  font-size: 16px; /* Размер шрифта */
  font-weight: bold; /* Жирный шрифт */
  z-index: 1000; /* Убедитесь, что кнопка находится поверх остальных элементов */
}

.back-button:hover {
  background-color: rgba(65, 179, 164, 0.7529411765); /* Цвет фона при наведении */
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.hidden {
  display: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.clear-list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 40px;
}

.drink2go-container {
  margin: 0 auto;
  width: 320px;
}
@media (min-width: 768px) {
  .drink2go-container {
    width: 768px;
  }
}
@media (min-width: 1440px) {
  .drink2go-container {
    width: 1440px;
  }
}

.header__container {
  display: flex;
  flex-direction: row;
  margin-bottom: 15px;
  margin-top: 10px;
  width: 99%;
  height: 85px;
  background-color: #41b3a3;
  border-radius: 15px 15px 15px 15px;
  border-bottom: 5px solid #1beed0;
  border-right: 5px solid #1beed0;
}

.header__wrapper-logo {
  margin: auto;
  margin-left: 0;
}

.header__title {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: aliceblue;
  font-size: 28px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 2px;
  text-shadow: 2px 2px 4px rgba(215, 230, 225, 0.5);
}

.header__logo {
  cursor: default;
}

.nav__icon {
  position: absolute;
  width: 40px;
  height: 40px;
  margin: auto;
  top: 50%;
  left: 50%;
}

.nav__link:hover .nav__icon--telegram {
  stroke: #808080; /* Серый цвет обводки */
}

.nav__item {
  position: relative;
  margin: auto;
}

.nav__link {
  width: 100%;
  height: 100%;
}

.nav {
  margin: auto;
  margin-right: 0;
  width: 100px;
  height: 40px;
}

.main-block__container {
  position: relative;
  margin: 0;
  padding-left: 0;
  padding-right: 0;
  background-color: #85dcbc;
  height: 720px;
  border-radius: 15px 15px 15px 15px;
  border-bottom: 5px solid #1beed0;
  border-right: 5px solid #1beed0;
}
@media (max-width: 1440px) {
  .main-block__container {
    height: 860px;
  }
}
@media (max-width: 1023px) {
  .main-block__container {
    height: 1400px;
  }
}
@media (max-width: 770px) {
  .main-block__container {
    height: 1600px;
  }
}

.main-block {
  width: 99%;
  max-width: 1920px;
  margin: 0 auto;
}

.main-block__svg {
  width: 100%;
  height: 100%;
  border-radius: 15px 15px 15px 15px;
}

.main-block__wrapper {
  display: flex;
  flex-direction: row;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  position: absolute;
  z-index: 1;
  background-color: rgba(240, 248, 255, 0.53);
  width: 100%;
  height: 100%;
  border-radius: 15px 15px 15px 15px;
}
@media (max-width: 1023px) {
  .main-block__wrapper {
    flex-direction: column;
  }
}

.main-block__text {
  text-transform: uppercase;
  font-weight: 400;
  font-size: 22px;
  margin: auto;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: aliceblue;
  padding: 0;
}
@media (max-width: 1440px) {
  .main-block__text {
    margin-left: 0;
  }
}

.main-block__img {
  width: 50px;
  height: 40px;
  margin: auto;
  padding-right: 10px;
}
@media (max-width: 1440px) {
  .main-block__img {
    margin: 0;
  }
}

.main-block__info {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: aliceblue;
  width: 45%;
  height: 80%;
  margin: auto;
  margin-left: 60px;
  border-radius: 15px 15px 15px 15px;
  border-bottom: 5px solid #1beed0;
  border-right: 5px solid #1beed0;
  background-color: rgba(65, 179, 164, 0.7725490196);
}
@media (max-width: 1440px) {
  .main-block__info {
    border-right: none;
    width: 65%;
    margin-right: 0;
    border-radius: 15px 0 0 15px;
  }
}
@media (max-width: 1023px) {
  .main-block__info {
    width: 80%;
    margin: auto;
    margin-top: 80px;
    margin-bottom: 0;
    border-bottom: none;
    border-right: 5px solid #1beed0;
    border-radius: 15px 15px 0 0;
  }
}

.main-block__img-git {
  border-radius: 5px 5px 5px 5px;
}

.main-block__git-wrapper {
  display: flex;
  flex-direction: row;
  width: 100%;
  padding: 40px;
  margin: auto;
  font-size: 24px;
  font-weight: 400;
  letter-spacing: 2px;
  justify-content: space-between;
}
@media (max-width: 1440px) {
  .main-block__git-wrapper {
    flex-direction: column;
    height: 100%;
  }
}
@media (max-width: 1023px) {
  .main-block__git-wrapper {
    height: auto;
    flex-direction: row;
  }
}
@media (max-width: 770px) {
  .main-block__git-wrapper {
    flex-direction: column;
    height: 100%;
  }
}

.main-block__git-description {
  margin: auto;
  margin-right: 0;
}
@media (max-width: 1440px) {
  .main-block__git-description {
    margin-bottom: auto;
  }
}
@media (max-width: 1440px) {
  .main-block__git-description {
    margin: auto;
    margin-right: 0;
  }
}
@media (max-width: 770px) {
  .main-block__git-description {
    margin: auto;
  }
}

.main-block__list {
  width: 100%;
  padding-left: 40px;
  padding-top: 40px;
  padding-bottom: 40px;
  padding-right: 40px;
  margin: auto;
  font-size: 24px;
  font-weight: 400;
  letter-spacing: 2px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
@media (max-width: 1440px) {
  .main-block__list {
    flex-direction: column;
  }
}
@media (max-width: 1023px) {
  .main-block__list {
    flex-direction: row;
  }
}
@media (max-width: 770px) {
  .main-block__list {
    flex-direction: column;
  }
}

.main-block__link:hover .main-block__text {
  text-shadow: -1px -1px 0 #808080, 1px -1px 0 #808080, -1px 1px 0 #808080, 1px 1px 0 #808080; /* Серая обводка */
}

.main-block__link {
  height: 40px;
  display: flex;
  flex-direction: row;
}
@media (max-width: 1440px) {
  .main-block__link {
    margin-bottom: 10px;
  }
}
@media (max-width: 1023px) {
  .main-block__link {
    margin-bottom: 0;
  }
}
@media (max-width: 770px) {
  .main-block__link {
    margin-bottom: 20px;
  }
}

