  height: 50%;
  margin: auto;
  margin-top: 0;
  border-radius: 15px 15px 15px 15px;
  border-bottom: 5px solid #1beed0;
  border-right: 5px solid #1beed0;
  background-color: rgba(65, 179, 164, 0.7725490196);
}
