  position: absolute;
  width: 11%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.041);
  border: none;
  border-radius: 15px 15px 15px 15px;
}

.portfolio__button-img {
  position: relative;
  margin: auto;
  width: 48%;
  height: 10%;
}

.portfolio__button--prev {
  position: absolute;
  padding: 0;
  left: 0;
  top: 0;
