  background-color: rgba(0, 0, 0, 0);
}
.drink2go-nav--opened .drink2go-nav__toggle::before, .drink2go-nav--opened .drink2go-nav__toggle::after {
  content: "";
  position: absolute;
  top: 30px;
  left: 22px;
  width: 16px;
  height: 2px;
  background-color: #333333;
}
.drink2go-nav--opened .drink2go-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.drink2go-nav--opened .drink2go-nav__toggle::after {
  transform: rotate(-45deg);
}

